/*@ngInject*/
function supplementalApi(ssApiSvc, buildConfig) {
    // #----------------------------------------# //
    // #---- API Endpoint (ssSupplimentalsApi) ----# //

        // base endpoint url
        const baseUrl = buildConfig.endpoint.employees;

        const queryUrl = `${baseUrl}/organizations/:ORGID:/supplementals`;

        // model data type
        const model = 'supplementals';

        // possible relationships.
        const rel = [];

        const query = ssApiSvc.endpoint({url: queryUrl, method: 'GET'}, model, rel);

        // API endpoint methods
        return {
            query
        };

    // #-- END API Endpoint (ssSupplimentalsApi) --# //
    // #----------------------------------------# //
    }

    angular.module('super-suit-apis').factory('ssSupplementalsApi', supplementalApi);
