import {Enums} from 'base-enum';

// #-----------------------------------# //
// #---- Enums (/supplementalType) ----# //

class SupplementalTypeEnum extends Enums {
	/*@ngInject*/
	constructor() {
		var enums = [{
			name: 'Extended Days',
			value: 0
		}, {
			name: 'Extra Duty',
			value: 1
		}, {
			name: 'Incentive Payment',
			value: 2
		}, {
			name: 'Longevity',
			value: 3
		}, {
			name: 'Qualification',
			value: 4
		}
	];
		super(enums);
	}
}

// #---- End Enums (/supplementalType) ----# //
// #---------------------------------------# //

angular.module('super-suit-enums').factory('enumSupplementalType', () => new SupplementalTypeEnum());
